.prose p {
  margin-bottom: 1rem;
  line-height: 1.7;
}
.prose code {
  background: var(--muted);
  padding: 0.125rem 0.25rem;
  border-radius: 0.25rem;
  font-size: 0.875rem;
  font-family: var(--font-mono);
}
.prose pre {
  background: var(--muted);
  padding: 1rem;
  border-radius: 0.5rem;
  overflow-x: auto;
  margin: 1rem 0;
}
.prose pre code {
  background: none;
  padding: 0;
}
.prose blockquote {
  border-left: 4px solid var(--primary);
  padding-left: 1rem;
  margin: 1rem 0;
  font-style: italic;
  color: var(--muted-foreground);
}
.prose ul,
.prose ol {
  margin: 1rem 0;
  padding-left: 1.5rem;
}
.prose li {
  margin: 0.5rem 0;
}
.prose a {
  color: var(--primary);
  text-decoration: underline;
  text-decoration-color: transparent;
  transition: text-decoration-color 0.2s;
}
.prose a:hover {
  text-decoration-color: var(--primary);
}
.prose table {
  width: 100%;
  border-collapse: collapse;
  margin: 1rem 0;
}
.prose th,
.prose td {
  border: 1px solid var(--border);
  padding: 0.5rem;
  text-align: left;
}
.prose th {
  background: var(--muted);
  font-weight: 600;
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}
.focus-visible:focus {
  outline: 2px solid hsl(var(--ring));
  outline-offset: 2px;
}
.glass {
  background: hsla(var(--glass));
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid hsla(var(--glass-light));
}
.glass-light {
  background: hsla(var(--glass-light));
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid hsla(var(--glass-light));
}
.glass-dark {
  background: hsla(var(--glass-dark));
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid hsla(var(--glass-dark));
}
.gradient-primary {
  background: linear-gradient(135deg, hsl(var(--gradient-from)), hsl(var(--gradient-via)), hsl(var(--gradient-to)));
}
.gradient-primary-radial {
  background: radial-gradient(ellipse at center, hsl(var(--gradient-from)), hsl(var(--gradient-via)), hsl(var(--gradient-to)));
}
.gradient-text {
  background: linear-gradient(135deg, hsl(var(--gradient-from)), hsl(var(--gradient-to)));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.nav-float {
  position: fixed;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  z-index: 50;
}
.nav-item {
  background: hsla(var(--glass));
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid hsla(var(--glass-light));
  border-radius: 0.5rem;
  padding: 0.75rem;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
}
.nav-item:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px hsl(var(--primary) / 0.3);
}
.nav-item.active {
  background: hsl(var(--primary));
  color: hsl(var(--primary-foreground));
  box-shadow: 0 0 20px hsl(var(--primary) / 0.3);
}
.card-glass {
  background: hsla(var(--glass));
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid hsla(var(--glass-light));
  border-radius: 0.5rem;
  padding: 1.5rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transition: all 0.3s ease;
}
.card-glass:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.card-float {
  background: hsla(var(--glass));
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid hsla(var(--glass-light));
  border-radius: 0.5rem;
  padding: 1.5rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transition: all 0.3s ease;
}
.card-float:hover {
  animation: glass-float 3s ease-in-out infinite;
}
.btn-glass {
  background: hsla(var(--glass));
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid hsla(var(--glass-light));
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  transition: all 0.3s ease;
}
.btn-glass:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px hsl(var(--primary) / 0.3);
}
.btn-primary {
  background: hsl(var(--primary));
  color: hsl(var(--primary-foreground));
  box-shadow: 0 0 20px hsl(var(--primary) / 0.3);
}
.btn-primary:hover {
  background: hsl(var(--primary) / 0.9);
}
.btn-gradient {
  background: linear-gradient(135deg, hsl(var(--gradient-from)), hsl(var(--gradient-via)), hsl(var(--gradient-to)));
  color: white;
  box-shadow: 0 0 20px hsl(330 81% 60% / 0.3);
}
.btn-gradient:hover {
  transform: scale(1.05);
}
.shimmer {
  background: linear-gradient(
    90deg,
    transparent,
    hsla(var(--glass-light)),
    transparent
  );
  background-size: 200% 100%;
  animation: shimmer 2s infinite;
}
@media (max-width: 768px) {
  .nav-float {
    position: fixed;
    bottom: 1rem;
    left: 1rem;
    right: 1rem;
    top: auto;
    transform: none;
    display: flex;
    justify-content: center;
    gap: 0.5rem;
  }
  .nav-item {
    flex: 1;
    text-align: center;
  }
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: hsla(var(--glass));
  border-radius: 4px;
}
::-webkit-scrollbar-thumb {
  background: hsla(var(--glass-light));
  border-radius: 4px;
  border: 1px solid hsla(var(--glass-dark));
}
::-webkit-scrollbar-thumb:hover {
  background: hsl(var(--primary) / 0.5);
}
